import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let store = new Vuex.Store({
    state: {
        list: [
            {
                id: 1,
                checked: false,
                visible: true,
                title: 'Task 1'
            },
            {
                id: 2,
                checked: false,
                visible: true,
                title: 'Task 2'
            },
            {
                id: 3,
                checked: false,
                visible: true,
                title: 'Task 3'
            },
            {
                id: 4,
                checked: false,
                visible: true,
                title: 'Task 4'
            },
            {
                id: 5,
                checked: false,
                visible: true,
                title: 'Task 5'
            },
        ]
    },
    mutations: {
        DELETE: (state, id) => {

            state.list.splice(id,1)

        },
        ADD: (state, data) => {

            state.list.push(data)

        },
        CHECKED: (state, data) => {

            let taskCurrent = state.list.find(el => el.id == data.id);

            taskCurrent.checked = data.checked

        },
    },
    actions: {
        TASK_DELETE({commit}, id){
            commit('DELETE', id)
        },
        TASK_ADD({commit}, data){
            commit('ADD', data)
        },
        TASK_CHECKED({commit}, data){
            commit('CHECKED', data)
        }
    },
    getters: {
        LIST(state){
            return state.list
        }
    }
})

export default store;
