<template>
    <section class="v-list">
        <div class="container">
            <div class="row">
                <div class="col-12">
                    <div class="search mb-3">
                        <h3>Search:</h3>
                        <input
                                type="text"
                                class="form-control"
                                placeholder="Search"
                                v-model="search"
                        >
                    </div>
                </div>
            </div>
            <div class="row">

                <div class="col-md-4 mb-4">
                    <div class="list-group">
                        <h3>List:</h3>
                        <label
                                class="list-group-item"
                                v-for="(task, index) in list"
                                :key="task.id"
                                :class="{ cross: task.checked}"
                                v-show="task.visible"
                        >
                            <input class="form-check-input me-1" type="checkbox" :checked="task.checked" @change="taskChecked(index, $event)">
                            <span>{{ task.title }}</span>
                            <button
                                    class="btn-close"
                                    @click="taskDelete(index)"
                            ></button>
                        </label>
                    </div>
                </div>

                <div class="col-md-3 mb-4">
                    <h3>Filters:</h3>
                    <div class="mb-1 form-check">
                        <input type="checkbox" class="form-check-input" v-model="filters" value="all" id="all">
                        <label class="form-check-label" for="all">All</label>
                    </div>
                    <div class="mb-1 form-check">
                        <input type="checkbox" class="form-check-input" v-model="filters" value="checked" id="checked">
                        <label class="form-check-label" for="checked">Checked</label>
                    </div>
                    <div class="mb-1 form-check">
                        <input type="checkbox" class="form-check-input" v-model="filters" value="active" id="active">
                        <label class="form-check-label" for="active">Active</label>
                    </div>
                </div>

                <div class="col-md-4 mb-4">
                    <h3>Add Task:</h3>
                    <form @submit.prevent="taskAdd">
                        <div class="mb-3">
                            <label class="form-label">Name Task</label>
                            <input
                                    type="text"
                                    class="form-control"
                                    :class="$v.task.$error ? 'is-invalid' : ''"
                                    v-model.trim="task"
                            >
                            <span
                                    class="form-error"
                                    v-if="$v.task.$dirty && !$v.task.required"
                            >
                                Input is required
                            </span>
                            <span
                                    class="form-error"
                                    v-if="$v.task.$dirty && !$v.task.minLength"
                            >
                                Min length 3
                            </span>
                        </div>
                        <button
                                class="btn btn-success"
                                type="submit"
                        >
                            Add New Task
                        </button>
                    </form>


                </div>

            </div>
        </div>
    </section>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex'
    import {required, minLength} from 'vuelidate/lib/validators'

    export default {
        name: "v-list",
        data(){
            return {
                task: '',
                search: '',
                filters: [],
            }
        },
        validations: {
            task: {required, minLength: minLength(3)}
        },
        computed: {
            ...mapGetters([
                'LIST'
            ]),
            list() {

                // const list = []
                //
                // this.LIST.map(task => {
                //
                //     let add = true;
                //
                //     if(this.filters.length){
                //         let addFilter = false
                //         if(this.filters.includes("checked") && task.checked){
                //             addFilter = true
                //         } else if(this.filters.includes("active") && !task.checked){
                //             addFilter = true
                //         } else if(this.filters.includes("all")){
                //             addFilter = true
                //         } else {
                //             return
                //         }
                //         add = addFilter
                //     }
                //
                //     if(add){
                //         list.push(task)
                //     }
                //
                // });
                // return list.filter(task => {
                //     return task.title.includes(this.search)
                // })



                return this.LIST.filter(task => {

                    if(this.filters.length){
                        if(this.filters.includes("checked") && task.checked){
                            task.visible = true
                        }else if(this.filters.includes("active") && !task.checked){
                            task.visible = true
                        } else if(this.filters.includes("all")){
                            task.visible = true
                        } else {
                            task.visible = false
                        }
                    }else{
                        task.visible = true
                    }

                    return task.title.includes(this.search)
                })


            }
        },
        methods: {
            ...mapActions([
                'TASK_DELETE',
                'TASK_ADD',
                'TASK_CHECKED'
            ]),
            taskDelete(index){
                this.TASK_DELETE(index)
            },
            taskAdd(){

                this.$v.task.$touch();

                if(!this.$v.task.$error){
                    let task = {
                        id: this.LIST.length + 1,
                        title: this.task,
                        checked: false,
                        visible: true
                    };

                    this.TASK_ADD(task)

                    // Reset Vuelidate
                    this.task = '';
                    this.$v.$reset();

                }
            },
            taskChecked(index, event){
                let taskCurrent = {
                    id: index + 1,
                    checked: event.target.checked
                }
                this.TASK_CHECKED(taskCurrent)
            }
        }
    }
</script>

